import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import ReactLoading from "react-loading";

export default function Step6(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const [loading, setloading] = useState(false);
  const [locations, setlocations] = useState([]);
  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();

    setErrorMsg(!isValid ? "Please valid Zip code" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  if (loading) {
    return (
      <MDBContainer className="mt-5">
        <MDBCol>
          <MDBRow></MDBRow>
        </MDBCol>
        <MDBCol center className="mt-5">
          <MDBRow center>
            <ReactLoading
              type={"spin"}
              color={"#2e56c8"}
              height={"10%"}
              width={"10%"}
            />
          </MDBRow>
        </MDBCol>
      </MDBContainer>
    );
  }
  return (
    <Step
      heading={`Fantastic, so we can schedule you at the location nearest to you what's your zip code?`}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow center>
                <MDBCol>
                  <MDBInput
                    label="Zip Code"
                    group
                    type="tel"
                    name="presentzipcode"
                    value={state.presentzipcode}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="z-depth-1a"
                    onClick={async () => {
                      setloading(true);
                      axios
                        .get(
                          `https://crm.hgscreenings.com/get-nearby-location?zipcode=${state.presentzipcode}`
                        )
                        .then((response) => {
                          setlocations(response.data.data);

                          let res = response.data.data[0];
                          setloading(false);
                          console.log(res);
                          onChangeField("e", "scheduledState", `${res.state}`);

                          onChangeField("e", "scheduledCity", `${res.city}`);

                          onChangeField(
                            "e",
                            "scheduledAddress",
                            `${res.address} ${res.state} ${res.city} ${res.zipcode}`
                          );
                          onChangeField("e", "location_id", res.id);
                          axios
                            .post(
                              `https://crm.hgscreenings.com/get-location-price`,
                              {
                                location_id: res.id,
                              }
                            )
                            .then((res) =>
                              onChangeField("e", "price", res.data.price)
                            )
                            .then(console.log(state.price));
                          if (typeof res !== "undefined") {
                            console.log("resoi");
                          } else {
                            history.push("/start/Noservice");
                          }
                          console.log(state.scheduledAddress);
                        });
                    }}
                  >
                    get nearest locations
                  </MDBBtn>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    onClick={() => setTouched(true)}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Get nearest locations
                  </MDBBtn>
                )}
              </MDBRow>
              {locations.length > 0 && (
                <>
                  <MDBRow>
                    <MDBCol className="mt-3" md={12}>
                      <select
                        className="browser-default custom-select"
                        label="Date of Birth"
                        group
                        type="date"
                        name="location"
                        value={state.location}
                        onChange={(e) => {
                          onChangeField(e, e.target.name, e.target.value);
                          const res = locations[e.target.value];
                          onChangeField("e", "scheduledState", `${res.state}`);

                          onChangeField("e", "scheduledCity", `${res.city}`);

                          onChangeField(
                            "e",
                            "scheduledAddress",
                            `${res.address} ${res.state} ${res.city} ${res.zipcode}`
                          );
                          onChangeField("e", "location_id", res.id);
                          axios
                            .post(
                              `https://crm.hgscreenings.com/get-location-price`,
                              {
                                location_id: res.id,
                              }
                            )
                            .then((res) =>
                              onChangeField("e", "price", res.data.price)
                            )
                            .then(console.log(state.price));
                          // history.push(
                        }}
                        required
                        containerClass="mb-0"
                      >
                        <option>Select</option>
                        {locations.map((t, index) => (
                          <option value={index}>{t.location_name}</option>
                        ))}
                      </select>
                    </MDBCol>
                  </MDBRow>
                  <MDBRow
                    center
                    className="d-flex flex-column align-items-center"
                  >
                    {touched && errorMsg.length > 0 && (
                      <div className="text-danger mb-2">{errorMsg}</div>
                    )}
                    {state.scheduledAddress.length > 0 && (
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="z-depth-1a"
                        onClick={() => {
                          history.push(nextStep);
                        }}
                      >
                        Next
                      </MDBBtn>
                    )}
                    {!state.scheduledAddress.length > 0 && (
                      <MDBBtn
                        color={"grey"}
                        onClick={() => setTouched(true)}
                        disabled
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    )}
                  </MDBRow>
                </>
              )}
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
