import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBCardHeader,
  MDBModalFooter,
  MDBContainer,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import axios from "axios";
import ReactLoading from "react-loading";

export default function Step17(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const [time, settime] = useState([]);
  const [loading, setloading] = useState(false);
  const [modal, setmodal] = useState(false);
  const convertTime12to24 = (time12h) => {
    const [time, modifier] = time12h.split(" ");

    let [hours, minutes] = time.split(":");

    if (hours === "12") {
      hours = "00";
    }

    if (modifier === "PM") {
      hours = parseInt(hours, 10) + 12;
    }

    return `${hours}:${minutes}`;
  };
  const ampm = time.map((t) => {
    if (t.substring(0, 2) >= 12) {
      if (t.substring(0, 2) == 12)
        return `${parseInt(t.substring(0, 2))}:${t.substring(3, 5)} PM`;
      return `${parseInt(t.substring(0, 2)) - 12}:${t.substring(3, 5)} PM`;
    } else {
      return t + " AM";
    }
  });

  const mindate = () => {
    var today = new Date();
    var dd = today.getDate();
    var mm = today.getMonth() + 1; //January is 0!
    var yyyy = today.getFullYear();
    if (dd < 10) {
      dd = "0" + dd;
    }
    if (mm < 10) {
      mm = "0" + mm;
    }

    return yyyy + "-" + mm + "-" + dd;
  };

  const maxdate = () => {
    var today = new Date();

    today.setDate(today.getDate() + 5);

    function formatDate(date) {
      let dd = String(date.getDate()).padStart(2, "0");
      let mm = String(date.getMonth() + 1).padStart(2, "0");
      let yyyy = date.getFullYear();
      return `${yyyy}-${mm}-${dd}`;
    }
    return formatDate(today);
  };

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();

    setErrorMsg(!isValid ? "Please enter date" : "");
  };
  const toggle = () => {
    setmodal(!modal);
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <>
      <Step
        heading={`Fantastic, the location nearest to you is our location located at  ${state.scheduledAddress} Please select the best date and time below for your test!`}
      >
        <img
          src={BackBtn}
          className="btn-back"
          onClick={() => history.push(prevStep)}
        />
        <MDBContainer>
          <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
            <MDBRow center>
              <MDBCol sm={10} md={8}>
                <div className="my-4" />
                <MDBRow center>
                  <MDBCol>
                    <MDBInput
                      label="Date"
                      group
                      type="date"
                      name="scheduleDate"
                      min={mindate()}
                      max={maxdate()}
                      value={state.scheduleDate}
                      onChange={(e) => {
                        validate();
                        onChangeField(e, e.target.name, e.target.value);
                        setloading(true);
                        axios
                          .get(
                            `https://crm.hgscreenings.com/get-scheduletime?date=${e.target.value}&location_id=${state.location_id}`
                          )
                          .then((res) => {
                            console.log(res);
                            if (res.data.result.length < 2) {
                              setmodal(true);
                            }
                            settime(res.data.result);
                            setloading(false);
                            console.log(res.data.result);
                          });
                      }}
                      required
                      containerClass="mb-0"
                    />
                  </MDBCol>
                </MDBRow>
                {loading && (
                  <MDBContainer className="mt-5">
                    <MDBCol>
                      <MDBRow></MDBRow>
                    </MDBCol>
                    <MDBCol center className="mt-5">
                      <MDBRow center>
                        <ReactLoading
                          type={"spin"}
                          color={"#2e56c8"}
                          height={"10%"}
                          width={"10%"}
                        />
                      </MDBRow>
                    </MDBCol>
                  </MDBContainer>
                )}
                {time.length > 1 && !loading && (
                  <>
                    <MDBRow>
                      <MDBCol className="mt-3">
                        <select
                          className="browser-default custom-select"
                          label="Date of Birth"
                          group
                          type="date"
                          name="scheduleTime"
                          value={state.scheduleTime}
                          onChange={(e) => {
                            onChangeField(e, e.target.name, e.target.value);
                            onChangeField(
                              e,
                              "serverTime",
                              convertTime12to24(e.target.value)
                            );
                          }}
                          required
                          containerClass="mb-0"
                        >
                          <option>Select</option>
                          {ampm.map((t, index) => (
                            <option>{t}</option>
                          ))}
                        </select>
                      </MDBCol>
                    </MDBRow>
                    <MDBRow
                      center
                      className="d-flex flex-column align-items-center"
                    >
                      {touched && errorMsg.length > 0 && (
                        <div className="text-danger mb-2">{errorMsg}</div>
                      )}
                      {state.serverTime.length > 1 && touched && !errorMsg && (
                        <Link to={nextStep}>
                          <MDBBtn
                            color="pink"
                            rounded
                            type="button"
                            className="z-depth-1a"
                          >
                            Next
                          </MDBBtn>
                        </Link>
                      )}
                      {(!touched || errorMsg) && (
                        <MDBBtn
                          color={"grey"}
                          disabled
                          rounded
                          type="button"
                          className="z-depth-1a"
                        >
                          Next
                        </MDBBtn>
                      )}
                    </MDBRow>
                  </>
                )}
              </MDBCol>
            </MDBRow>
          </form>
        </MDBContainer>
      </Step>
      <MDBContainer>
        <MDBModal isOpen={modal} toggle={toggle}>
          <MDBModalHeader toggle={toggle}>Oops!</MDBModalHeader>
          <MDBModalBody>
            <div>
              Unfortunately, we don't have any times available for your requsted
              date try selecting a different date to see our availability
            </div>
          </MDBModalBody>
          <MDBModalFooter>
            <MDBBtn color="secondary" onClick={toggle}>
              Change Date
            </MDBBtn>
          </MDBModalFooter>
        </MDBModal>
      </MDBContainer>
    </>
  );
}
