import React, { Component } from "react";
import { Route, Switch, Redirect, Prompt } from "react-router-dom";
import axios from "axios";
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step4 from "./Step4";
import Step5 from "./Step5";
import Step6 from "./Step6";
import Step7 from "./Step7";
import Step8 from "./Step8";
import Step9 from "./Step9";
import Step10 from "./Step10";

// import AllOptions from "./AllOptions";
import NoLocation from "./NoLocations";
import ThankYou from "./ThankYou";
import Decline from "./Decline";
export default class MultiStepForm extends Component {
  state = {
    // step1
    firstName: "",
    lastName: "",

    //step 2
    refresh: false,
    //step3
    latLng: {},
    streetAddress: "",
    Housenumb: "",
    Streetname: "",
    aptUnitNo: "",
    city: "",
    state: "",
    zipCode: "",

    // step8
    scheduleDate: "",
    scheduleTime: "",

    // step9
    paymentMethod: "",
    paymentzipcode: "",

    // step9
    phone: "",
    sex: "",
    presentzipcode: "",
    // step10
    email: "",
    location_id: "",
    // step10 -- [payment]
    cvv: "",
    exp: "",
    focused: "",
    nameonCard: "",
    creditCardNumber: "",
    scheduledAddress: "",
    scheduledCity: "",
    scheduledState: "",
    // step12
    transactionid: "",
    // step18
    dob: "",
    price: "",
    serverTime: "",
    last4card: "",
    //step 20

    // step22

    // step25
    agree: "",
  };
  handleChangeField = (e, name, value) => {
    this.setState(
      {
        [name]: value,
      },
      () => console.log(this.state)
    );
  };

  componentDidUpdate = () => {
    if (true) {
      window.onbeforeunload = () => true;
    } else {
      window.onbeforeunload = undefined;
    }
  };

  constructor(props) {
    super(props);
    if (window.performance) {
      if (performance.navigation.type == 1) {
        this.handleChangeField("e", "refresh", true);
        this.props.history.push("/start/1");
      } else {
      }
    }
  }

  submitData() {
    const data = {
      firstname: this.state.firstName,
      lastname: this.state.lastName,
      last4card: this.state.last4card,
      address: ` ${this.state.Housenumb} ${this.state.Streetname} `,
      paymenttype: this.state.paymentMethod,
      phonenumber: this.state.phone,
      email: this.state.email,
      dob: this.state.dob,
      gender: this.state.sex,
      city: this.state.city,
      state: this.state.state,
      zipcode: this.state.zipCode,
      location: this.state.location_id,
      paidamount: this.state.price,
      transactionid: this.state.transactionid,
      scheduledate: `${this.state.scheduleDate} ${this.state.serverTime}:00`,
    };
    var config = {
      headers: {
        "Content-Type": "application/json",
        "Accept-Encoding": "gzip, deflate, br",
        "Access-Control-Allow-Origin": "*",
      },
    };
    axios
      .post(
        "https://crm.hgscreenings.com/add-schedule",
        JSON.stringify(data),
        config
      )
      .then((response) => console.log(response))
      .finally(() => console.log("Data Sent", { data: data }));
  }

  renderStep = (props) => {
    const { history } = props;
    const { path, params } = props.match;
    if (params.step === "thankyou") {
      this.submitData();
      return <ThankYou state={this.state} />;
    } else if (params.step === "decline") {
      return <Decline />;
    } else if (params.step === "Noservice") {
      return <NoLocation />;
    }
    const step = parseInt(params.step);
    const currentPath = `/${path.split("/")[1]}`;
    switch (step) {
      case 1:
        return (
          <Step1
            history={history}
            nextStep={`${currentPath}/2`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 2:
        return (
          <Step2
            history={history}
            prevStep={`${currentPath}/1`}
            nextStep={`${currentPath}/3`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 3:
        console.log("step 3");

        return (
          <Step3
            history={history}
            prevStep={`${currentPath}/2`}
            nextStep={`${currentPath}/4`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 4:
        console.log("step 4");
        return (
          <Step4
            history={history}
            prevStep={`${currentPath}/3`}
            nextStep={`${currentPath}/5`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 5:
        return (
          <Step5
            history={history}
            prevStep={`${currentPath}/4`}
            nextStep={`${currentPath}/6`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 6:
        return (
          <Step6
            history={history}
            prevStep={`${currentPath}/5`}
            nextStep={`${currentPath}/7`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 7:
        return (
          <Step7
            history={history}
            prevStep={`${currentPath}/6`}
            nextStep={`${currentPath}/8`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 8:
        return (
          <Step8
            history={history}
            prevStep={`${currentPath}/7`}
            nextStep={`${currentPath}/9`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 9:
        return (
          <Step9
            history={history}
            prevStep={`${currentPath}/8`}
            nextStep={`${currentPath}/10`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 10:
        return (
          <Step10
            history={history}
            prevStep={`${currentPath}/9`}
            nextStep={`${currentPath}/thankyou`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      default:
        return <div>invalid step</div>;
    }
  };
  render() {
    const { path } = this.props.match;
    return (
      <React.Fragment>
        <Switch>
          <Route
            path="/start"
            exact
            render={() => <Redirect to={`${path}/1`} />}
          />
          <Route
            path={`${path}/:step`}
            render={(props) => (
              <>
                <Prompt
                  when={this.state.refresh}
                  message="Refreshing will make you restart the scheduling process"
                />
                {this.renderStep(props)}
                <small
                  style={{
                    position: "relativr",
                    display: "flex",
                    justifyContent: "center",
                    width: "100%",
                    bottom: "-49vh",
                    position: "relative",
                    textAlign: "center",
                    padding: 10,
                  }}
                >
                  If you have trouble scheduling an appointment please call us
                  at 877-732-8258
                </small>
              </>
            )}
          />
        </Switch>
      </React.Fragment>
    );
  }
}
